.Map {
  min-width: 500px;
  width: 100%;
  margin-bottom: 2em;
  height: 100%;
  background: white;
  padding: 0.6em 1em;
  margin: 0.5em;
}
.earth {
  fill: #e2f1f1;
  stroke: none;
}

.graticule {
  stroke: #cadddd;
  fill: none;
}

.my-location {
  fill: #8f97db;
  stroke: white;
}

.country:hover {
  fill: cornflowerblue;
}

circle {
  pointer-events: none;
}

.tooltip {
  opacity: 0;
  position: absolute;
  top: -12px;
  left: 0;
  padding: 0.6em 1em;
  background: #fff;

  text-align: center;
  border: 1px solid #ddd;
  z-index: 10;
  transition: all 0.2s ease-out;
  pointer-events: none;
  min-width: 6rem;
}

.tooltip:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 12px;
  height: 12px;
  background: white;
  border: 1px solid #ddd;
  border-top-color: transparent;
  border-left-color: transparent;
  transform: translate(-50%, 50%) rotate(45deg);
  transform-origin: center center;
  z-index: 10;
}

.tooltip-country {
  margin-bottom: 0.2em;
  font-weight: 600;
}

.tooltip-value {
  font-size: 0.8em;
}

.legend-title {
  fill: #576574;
  text-anchor: middle;
  font-weight: 900;
  font-size: 0.95em;
  letter-spacing: 0.01em;
}

.legend-byline {
  fill: #576574;
  opacity: 0.6;
  text-anchor: middle;
  font-size: 0.7em;
}

.legend-value {
  fill: #576574;
  // dominant-baseline: middle;
  font-size: 0.8em;
}

.legend-value:first-of-type {
  text-anchor: end;
}
